/* Import font */
@import url('https://fonts.googleapis.com/css2?family=Crimson+Text:wght@400;600&display=swap');

/* Global Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Root Variables - Warm Neutral Palette */
:root {
  /* Primary Backgrounds - Warm foundation */
  --bg-primary: #fffef2;
  --bg-secondary: #f6f5e8;
  --bg-subtle: #ebeade;
  
  /* Text Hierarchy - Rich contrast */
  --text-primary: #333333;
  --text-secondary: #666666;
  --text-light: #bcbbb4;
  --text-meta: #4a4a4a;
  
  /* Interactive States - Minimal emphasis */
  --interactive-base: #333333;
  --interactive-hover: #000000;
  --focus-outline: #252525;
  
  /* Borders & Separators */
  --border-light: #bcbbb4;
  --border-medium: #cccccc;
  --border-subtle: rgba(51,51,51,0.2);
}

/* Body Styling */
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--bg-primary);
  color: var(--text-primary);
  line-height: 1.6;
}

/* Typography System */
.hero-large {
  font-size: 48px;
  font-weight: 400;
  line-height: 1.2;
  color: var(--text-primary);
  margin-bottom: 24px;
}

.hero-subtitle {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.6;
  color: var(--text-secondary);
  margin-bottom: 32px;
  max-width: 700px;
}

.heading-1 {
  font-size: 36px;
  font-weight: 400;
  line-height: 1.2;
  color: var(--text-primary);
  margin-bottom: 24px;
}

.heading-2 {
  font-size: 28px;
  font-weight: 400;
  line-height: 1.2;
  color: var(--text-primary);
  margin-bottom: 16px;
}

.heading-3 {
  font-size: 20px;
  font-weight: 400;
  line-height: 1.2;
  color: var(--text-primary);
  margin-bottom: 12px;
}

.body-large {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.7;
  color: var(--text-primary);
}

.body-regular {
  font-size: 16px;
  font-weight: 400;
  line-height: 1.6;
  color: var(--text-primary);
}

.body-small {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.4;
  color: var(--text-secondary);
}

/* Navigation */
.navigation-header {
  background: var(--bg-primary);
  border-bottom: 1px solid var(--border-light);
  padding: 24px 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 100;
}

.navigation-logo {
  font-size: 22px;
  font-weight: 400;
  color: var(--text-primary);
  text-decoration: none;
}

.navigation-menu {
  display: flex;
  align-items: center;
  gap: 40px;
}

.navigation-link {
  color: var(--text-primary);
  text-decoration: none;
  font-size: 15px;
  font-weight: 400;
  padding: 8px 0;
  position: relative;
  transition: color 0.2s ease;
}

.navigation-link:after {
  content: '';
  position: absolute;
  bottom: 4px;
  left: 0;
  width: 0;
  height: 1px;
  background: var(--text-primary);
  transition: width 0.3s ease-in-out;
}

.navigation-link:hover:after {
  width: 100%;
}

/* Buttons */
.btn-primary {
  background: transparent;
  color: var(--interactive-base);
  border: 1px solid var(--interactive-base);
  border-radius: 0;
  padding: 18px 32px;
  min-width: 200px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s cubic-bezier(.645,.045,.355,1);
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.btn-primary:hover {
  background: var(--interactive-base);
  color: var(--bg-primary);
}

/* Container */
.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 40px;
}

/* Hero Section */
.hero-section {
  background: var(--bg-secondary);
  padding: 120px 0;
  min-height: 600px;
  display: flex;
  align-items: center;
}

.hero-content {
  max-width: 800px;
}

/* Section Padding */
.section-padding {
  padding: 100px 0;
}

/* Grid Layouts */
.grid-two-column {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 80px;
  align-items: start;
}

.content-spacing > * + * {
  margin-top: 20px;
}

/* Vision Section */
.vision-values {
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.value-item {
  padding: 32px;
  background: var(--bg-secondary);
  border-radius: 0;
}

/* Problem Section */
.problem-section {
  background: var(--bg-subtle);
  padding: 100px 0;
}

.problem-content {
  text-align: left;
}

.problem-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 48px;
  margin-top: 60px;
}

.problem-item {
  padding: 32px;
  background: var(--bg-primary);
  border-radius: 0;
}

/* Products Section */
.section-header {
  text-align: left;
  margin-bottom: 60px;
  max-width: 700px;
}

.grid-product-showcase {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 40px;
}

.product-card {
  background: var(--bg-primary);
  border: 1px solid var(--border-light);
  border-radius: 0;
  overflow: hidden;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  cursor: pointer;
}

.product-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.08);
}

.product-card-image {
  width: 100%;
  height: 320px;
  object-fit: cover;
}

.product-card-category {
  font-size: 12px;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 20px 24px 0 24px;
}

.product-card-title {
  font-size: 20px;
  font-weight: 400;
  line-height: 1.3;
  margin: 8px 0;
  color: var(--text-primary);
  padding: 0 24px;
}

.product-card-description {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5;
  color: var(--text-secondary);
  padding: 0 24px 24px 24px;
}

/* Process Section */
.process-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 48px;
  margin-top: 60px;
}

.process-item {
  padding: 40px 32px;
  background: var(--bg-secondary);
  border-radius: 0;
  text-align: left;
}

.process-icon {
  color: var(--text-primary);
  margin-bottom: 20px;
}

/* Sustainability Section */
.sustainability-section {
  background: var(--bg-secondary);
  padding: 100px 0;
}

/* Nirjaram Section */
.nirjaram-section {
  padding: 100px 0;
  background: var(--bg-subtle);
}

.nirjaram-content {
  max-width: 800px;
  margin: 0 auto;
  text-align: left;
}

.nirjaram-content > * + * {
  margin-top: 20px;
}

/* Contact Section */
.contact-section {
  background: var(--bg-primary);
}

.contact-content-centered {
  max-width: 700px;
  margin: 0 auto;
  text-align: center;
}

.contact-content-centered > * + * {
  margin-top: 20px;
}

.contact-email-display {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 16px;
  margin-top: 40px;
  color: var(--text-primary);
}

.contact-email-link {
  font-size: 24px;
  font-weight: 400;
  color: var(--interactive-base);
  text-decoration: none;
  position: relative;
  transition: color 0.2s ease;
}

.contact-email-link:hover {
  color: var(--interactive-hover);
}

.contact-email-link:after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 0;
  height: 1px;
  background: var(--interactive-base);
  transition: width 0.3s ease-in-out;
}

.contact-email-link:hover:after {
  width: 100%;
}

/* Footer */
.footer {
  background: var(--bg-secondary);
  padding: 60px 0;
  border-top: 1px solid var(--border-light);
}

.footer-content {
  display: flex;
  justify-content: space-between;
  align-items: start;
}

.footer-brand {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.footer-tagline {
  margin-top: 8px;
  font-style: italic;
  color: var(--text-secondary);
}

.footer-contact {
  text-align: right;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .navigation-menu {
    display: none;
  }
  
  .grid-two-column {
    grid-template-columns: 1fr;
    gap: 48px;
  }
  
  .problem-grid {
    grid-template-columns: 1fr;
    gap: 32px;
  }
  
  .hero-large {
    font-size: 36px;
  }
  
  .heading-1 {
    font-size: 32px;
  }
  
  .section-padding {
    padding: 80px 0;
  }
  
  .container {
    padding: 0 24px;
  }
}

@media (max-width: 640px) {
  .hero-large {
    font-size: 28px;
  }
  
  .heading-1 {
    font-size: 26px;
  }
  
  .heading-2 {
    font-size: 22px;
  }
  
  .grid-product-showcase {
    grid-template-columns: 1fr;
    gap: 32px;
  }
  
  .process-grid {
    grid-template-columns: 1fr;
    gap: 32px;
  }
  
  .section-padding {
    padding: 60px 0;
  }
  
  .hero-section {
    padding: 80px 0;
    min-height: 500px;
  }
  
  .container {
    padding: 0 16px;
  }
  
  .navigation-header {
    padding: 20px 16px;
  }
  
  .btn-primary {
    width: 100%;
  }
  
  .footer-content {
    flex-direction: column;
    gap: 32px;
  }
  
  .footer-contact {
    text-align: left;
  }
  
  .contact-email-link {
    font-size: 18px;
  }
}